.App {
  padding: 48px;
  padding-top: 24px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.youtube-signin-button {
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 12px;
  border: 3px solid #bbb; /* rgb(72, 133, 237, 0.4); */
  color: #333;
  margin: auto;
}

.youtube-signin-button:hover {
  background: #333;
  color: #ddd;
}

.youtube-signin-button img {
  width: 32px;
  margin-left: 12px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



